import React, { useState } from 'react';
import {
  Box,
  Typography,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Link,
  TextField,
  Button,
  Alert,
  Snackbar,
  InputBase,
} from '@mui/material';
import {
  Description,
  Refresh,
} from '@mui/icons-material';
import { RequiredInformationHeader } from './RequiredInformationHeader';
import { CompOffLayout } from './CompOffLayout.jsx';
import CompOffReport from './CompOffReport';

const CompOffManagerApprove = ({ onBack, submittedRequests = [] }) => {
  const [showReport, setShowReport] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success'
  });
  const [rmRemarks, setRmRemarks] = useState({});
  const [approvalStatus, setApprovalStatus] = useState({});

  // Convert submitted requests to approval format using the data from state 1
  const approvalRequests = submittedRequests.map((request, index) => ({
    id: request.id,
    employeeId: '22454221', // Default employee ID
    employeeName: 'Karandeep Sahni', // Default employee name
    requestDate: request.requestDate,
    reason: request.reason,
    initiationType: request.initiationType || 'forMe', // Add initiation type
    reportee: request.reportee || '' // Add reportee field
  }));

  // Check if any requests are "For My Reportee" to show Reportee column
  const hasReporteeRequests = approvalRequests.some(request => request.initiationType === 'forMyReportee');

  const handleRmRemarksChange = (id, value) => {
    setRmRemarks(prev => ({
      ...prev,
      [id]: value
    }));
  };

  const handleApproveRequest = (requestId) => {
    setApprovalStatus(prev => ({
      ...prev,
      [requestId]: 'approved'
    }));
    setSnackbar({
      open: true,
      message: 'Request approved successfully',
      severity: 'success'
    });
  };

  const handleRejectRequest = (requestId) => {
    setApprovalStatus(prev => ({
      ...prev,
      [requestId]: 'rejected'
    }));
    setSnackbar({
      open: true,
      message: 'Request rejected',
      severity: 'error'
    });
  };

  const handleTransferWorkflow = () => {
    setShowReport(true);
  };

  const handleBackFromReport = () => {
    setShowReport(false);
  };

  // Prepare data for report - only approved/rejected requests
  const getReportData = () => {
    return approvalRequests
      .filter(request => approvalStatus[request.id] === 'approved' || approvalStatus[request.id] === 'rejected')
      .map(request => ({
        ...request,
        rmRemarks: approvalStatus[request.id] === 'approved' ? 'Approved' : 'Rejected',
        status: approvalStatus[request.id]
      }));
  };

  return (
    <>
      {showReport ? (
        <CompOffReport 
          onBack={handleBackFromReport} 
          approvedRequests={getReportData()}
          initiationType={submittedRequests[0]?.initiationType || 'forMe'}
        />
      ) : (
        <CompOffLayout 
          title="Comp off Pre Approval - (Manager Approve)" 
          showBackButton={true} 
          onBackClick={onBack}
        >

        {/* Required Information */}
        <Box sx={{ bgcolor: '#F9FAFB', borderRadius: 2, p: 3, mb: 3, boxShadow: '0 1px 2px rgba(16,30,115,0.04)' }}>
          <RequiredInformationHeader />

          {/* Approval List Table */}
          <Box sx={{ mt: 2, mb: 2 }}>
            <Typography sx={{ fontWeight: 700, fontSize: 16, color: '#111827', mb: 2 }}>
              Approval List
            </Typography>
            <Box sx={{
              border: '1px solid #E5E7EB',
              borderRadius: '16px',
              overflow: 'hidden',
              bgcolor: '#FFFFFF',
              boxShadow: '0 1px 2px rgba(16,30,115,0.04)'
            }}>
              <Box sx={{
                display: 'flex',
                bgcolor: '#F1F6FB',
                p: 1,
                borderTopLeftRadius: '16px',
                borderTopRightRadius: '16px',
                fontWeight: 700,
                fontSize: 15,
                color: '#111827'
              }}>
                <Box sx={{ flex: 1 }}>Employee ID</Box>
                {hasReporteeRequests && <Box sx={{ flex: 1 }}>Reportee</Box>}
                <Box sx={{ flex: 1 }}>Employee Name</Box>
                <Box sx={{ flex: 1 }}>Request Date</Box>
                <Box sx={{ flex: 2 }}>Reason</Box>
                <Box sx={{ flex: 1 }}>RM Remarks</Box>
                <Box sx={{ flex: 1 }}>Action</Box>
              </Box>
              {/* Table Rows */}
              {approvalRequests.length > 0 ? (
                approvalRequests.map((req) => (
                  <Box key={req.id} sx={{ display: 'flex', alignItems: 'center', borderTop: '1px solid #E5E7EB', p: 1 }}>
                    <Box sx={{ flex: 1, fontSize: 14, color: '#111827' }}>{req.employeeId}</Box>
                    {hasReporteeRequests && (
                      <Box sx={{ flex: 1, fontSize: 14, color: '#111827' }}>{req.reportee || 'xxx-xx-x-xx'}</Box>
                    )}
                    <Box sx={{ flex: 1, fontSize: 14, color: '#111827' }}>{req.employeeName}</Box>
                    <Box sx={{ flex: 1, fontSize: 14, color: '#111827' }}>{req.requestDate}</Box>
                    <Box sx={{ flex: 2, fontSize: 14, color: '#111827' }}>{req.reason}</Box>
                    <Box sx={{ flex: 1, px: 1 }}>
                      <TextField
                        fullWidth
                        multiline
                        minRows={2}
                        maxRows={4}
                        size="small"
                        sx={{
                          '& .MuiOutlinedInput-root': {
                            '& fieldset': {
                              borderColor: '#3B82F6',
                              borderWidth: '2px',
                            },
                            '&:hover fieldset': {
                              borderColor: '#2563EB',
                            },
                            '&.Mui-focused fieldset': {
                              borderColor: '#2563EB',
                            },
                          },
                          '& .MuiInputBase-input': {
                            fontSize: 15,
                            color: '#333',
                          },
                        }}
                        value={rmRemarks[req.id] || ''}
                        onChange={(e) => handleRmRemarksChange(req.id, e.target.value)}
                        placeholder="Enter your remarks here..."
                      />
                    </Box>
                    <Box sx={{ flex: 1, display: 'flex', gap: 1, justifyContent: 'center' }}>
                      {approvalStatus[req.id] ? (
                        <Typography sx={{ 
                          fontSize: 12, 
                          fontWeight: 600,
                          color: approvalStatus[req.id] === 'approved' ? '#059669' : '#DC2626',
                          bgcolor: approvalStatus[req.id] === 'approved' ? '#D1FAE5' : '#FEE2E2',
                          px: 2,
                          py: 0.5,
                          borderRadius: 1
                        }}>
                          {approvalStatus[req.id] === 'approved' ? ' Approved' : ' Rejected'}
                        </Typography>
                      ) : (
                        <Box sx={{ display: 'flex', gap: 1 }}>
                          <Button
                            size="small"
                            variant="outlined"
                            sx={{ 
                              minWidth: 'auto',
                              px: 2,
                              py: 0.5,
                              fontSize: 12,
                              fontWeight: 600,
                              color: '#2563EB',
                              borderColor: '#3B82F6',
                              bgcolor: '#FFFFFF',
                              '&:hover': { borderColor: '#1D4ED8', bgcolor: '#F8FAFC' }
                            }}
                            onClick={() => handleRejectRequest(req.id)}
                          >
                            Reject
                          </Button>
                          <Button
                            size="small"
                            variant="contained"
                            sx={{ 
                              minWidth: 'auto',
                              px: 2,
                              py: 0.5,
                              fontSize: 12,
                              fontWeight: 600,
                              bgcolor: '#2563EB',
                              '&:hover': { bgcolor: '#1D4ED8' }
                            }}
                            onClick={() => handleApproveRequest(req.id)}
                          >
                            Approve
                          </Button>
                        </Box>
                      )}
                    </Box>
                  </Box>
                ))
              ) : (
                <Box sx={{ display: 'flex', alignItems: 'center', borderTop: '1px solid #E5E7EB', p: 1 }}>
                  <Box sx={{ flex: 1, fontSize: 14, color: '#111827', textAlign: 'center' }} colSpan={7}>
                    No requests submitted yet
                  </Box>
                </Box>
              )}
            </Box>
          </Box>

          {/* Comment Section */}
          <Box sx={{ mt: 3, mb: 2 }}>
            <Typography sx={{ fontWeight: 700, fontSize: 14, mb: 1 }}>Comment (Max 500 Chars)</Typography>
            <InputBase
              fullWidth
              multiline
              minRows={2}
              maxRows={4}
              sx={{
                bgcolor: '#FFFFFF',
                border: '1px solid #E5E7EB',
                borderRadius: 1,
                px: 2,
                py: 1.2,
                fontSize: 15
              }}
              placeholder="XXX-XXX-XX-XXX-X"
            />
          </Box>

          {/* Action Buttons */}
          <Box sx={{ display: 'flex', justifyContent: 'flex-end', gap: 2, mt: 3 }}>
            <Button
              variant="outlined"
              sx={{ 
                bgcolor: '#FFFFFF', 
                color: '#2563EB', 
                borderRadius: 2, 
                px: 5, 
                py: 1.5, 
                fontWeight: 700, 
                fontSize: 16, 
                textTransform: 'none', 
                boxShadow: 'none', 
                borderColor: '#3B82F6', 
                '&:hover': { bgcolor: '#F3F4F6' } 
              }}
              onClick={() => {
                // Handle reject all requests
                approvalRequests.forEach(req => {
                  if (!approvalStatus[req.id]) {
                    handleRejectRequest(req.id);
                  }
                });
              }}
            >
              Reject
            </Button>
            <Button
              variant="contained"
              sx={{ 
                bgcolor: '#2563EB', 
                color: '#FFFFFF', 
                borderRadius: 2, 
                px: 5, 
                py: 1.5, 
                fontWeight: 700, 
                fontSize: 16, 
                textTransform: 'none', 
                boxShadow: 'none', 
                '&:hover': { bgcolor: '#1D4ED8' } 
              }}
              onClick={() => {
                // Handle approve all requests
                approvalRequests.forEach(req => {
                  if (!approvalStatus[req.id]) {
                    handleApproveRequest(req.id);
                  }
                });
              }}
            >
              Approve
            </Button>
          </Box>






        </Box>

        {/* Transfer Workflow */}
        <Box sx={{ mt: 2, mb: 2 }}>
          <Box 
            sx={{ 
              display: 'flex', 
              alignItems: 'center', 
              justifyContent: 'space-between',
              bgcolor: '#F8FAFC',
              borderRadius: '8px',
              px: 3,
              py: 2,
              cursor: 'pointer',
              '&:hover': {
                bgcolor: '#F1F5F9'
              }
            }}
            onClick={handleTransferWorkflow}
          >
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Box sx={{ color: '#8B5CF6' }}>
                <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M10 4V16M4 10H16" stroke="#8B5CF6" strokeWidth="2" strokeLinecap="round"/>
                </svg>
              </Box>
              <Typography sx={{ 
                fontWeight: 500, 
                fontSize: 15, 
                color: '#374151',
                textTransform: 'none'
              }}>
                Transfer Workflow
              </Typography>
            </Box>
            <Box sx={{ color: '#374151' }}>
              <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M6 12L10 8L6 4" stroke="#374151" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
            </Box>
          </Box>
        </Box>

        {/* Snackbar */}
        <Snackbar
          open={snackbar.open}
          autoHideDuration={4000}
          onClose={() => setSnackbar({ ...snackbar, open: false })}
          anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        >
          <Alert
            onClose={() => setSnackbar({ ...snackbar, open: false })}
            severity={snackbar.severity}
            variant="filled"
          >
            {snackbar.message}
          </Alert>
        </Snackbar>
        </CompOffLayout>
      )}
    </>
  );
};

export default CompOffManagerApprove; 