import React from 'react';
import {
  Box,
  Typography,
  IconButton,
  Link,
} from '@mui/material';
import {
  Description,
  AccessTime,
} from '@mui/icons-material';

// CompOffLayout Component
export const CompOffLayout = ({ children, title, showBackButton = false, onBackClick }) => {
  const employeeData = {
    name: 'Manoj Kandan M',
    genId: '25504878',
    email: 'manoj.kandan@company.com',
    designation: 'Software Engineer',
    division: 'Technology',
    manager: 'John Manager'
  };

  return (
    <Box sx={{ display: 'flex', height: '100vh', bgcolor: '#F9FAFB' }}>
      {/* Main Content */}
      <Box sx={{ flex: 1, bgcolor: '#F9FAFB', p: { xs: 1, md: 3 } }}>
        {/* Breadcrumb */}
        <Typography sx={{ color: '#6B7280', fontSize: 13, mb: 1 }}>
          My Workspace {'>'} Comp off Pre Approval
        </Typography>
        
        {/* Title and Employee Info */}
        <Box sx={{ bgcolor: 'white', borderRadius: 2, p: 3, mb: 3, boxShadow: '0 1px 2px rgba(16,30,115,0.04)' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
            {showBackButton && (
              <IconButton onClick={onBackClick} sx={{ mr: 1, color: '#666' }}>
                
              </IconButton>
            )}
            <Typography sx={{ fontWeight: 700, fontSize: 20, color: '#111827', mr: 1 }}>
              {title}
            </Typography>
            <IconButton size="small" sx={{ color: '#00b0ff' }}>
              <AccessTime fontSize="small" />
            </IconButton>
          </Box>

          <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 4 }}>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Box sx={{ position: 'relative' }}>
                <Box sx={{ width: 40, height: 40, borderRadius: '50%', overflow: 'hidden', border: '1px solid #E5E7EB' }}>
                  <Box component="img" src="https://randomuser.me/api/portraits/men/32.jpg" alt="Manoj Kandan M" sx={{ width: '100%', height: '100%', objectFit: 'cover' }} />
                </Box>
                <Box
                  sx={{
                    position: 'absolute',
                    bottom: 0,
                    right: 0,
                    width: 12,
                    height: 12,
                    backgroundColor: '#4caf50',
                    borderRadius: '50%',
                    border: '2px solid white',
                  }}
                />
              </Box>
              <Box>
                <Typography sx={{ fontWeight: 700, fontSize: 15, color: '#111827' }}>{employeeData.name}  Gen ID: {employeeData.genId}</Typography>
                <Typography sx={{ fontSize: 13, color: '#6B7280' }}>{employeeData.email}</Typography>
              </Box>
            </Box>
            <Box sx={{ minWidth: 180 }}>
              <Typography sx={{ fontSize: 13, color: '#6B7280' }}>Designation</Typography>
              <Typography sx={{ fontWeight: 700, fontSize: 15, color: '#111827' }}>{employeeData.designation}</Typography>
            </Box>
            <Box sx={{ minWidth: 260 }}>
              <Typography sx={{ fontSize: 13, color: '#6B7280' }}>Division</Typography>
              <Typography sx={{ fontWeight: 700, fontSize: 15, color: '#111827' }}>{employeeData.division}</Typography>
            </Box>
            <Box sx={{ minWidth: 180 }}>
              <Typography sx={{ fontSize: 13, color: '#6B7280' }}>Manager</Typography>
              <Typography sx={{ fontWeight: 700, fontSize: 15, color: '#111827' }}>{employeeData.manager}</Typography>
            </Box>
          </Box>
        </Box>

        {/* Content */}
        {children}
        
        {/* View Policies Link - Reusable component */}
        <Box sx={{ mt: 2 }}>
          <Link href="#" underline="hover" sx={{ display: 'flex', alignItems: 'center', gap: 1, color: '#6B7280' }}>
            <Description fontSize="small" />
            View Policies
          </Link>
        </Box>
      </Box>
    </Box>
  );
}; 